.actu {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000; }
  .actu__el {
    position: relative;
    display: block;
    width: 100%;
    height: 49px;
    background: red; }
    .actu__el--img {
      display: block;
      position: absolute;
      width: 49px;
      height: 100%;
      top: 0;
      left: 0;
      border-radius: 100%;
      background: green;
      overflow: hidden; }
      .actu__el--img img {
        display: block;
        position: relative;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0; }
    .actu__el--post {
      position: absolute;
      width: calc(100% - 59px);
      height: 100%;
      top: 0;
      right: 0;
      background: blue; }
